import {  
    mapMutations  
} from 'vuex';

export default{
	data(){
		return {
			mobile:'',
			imgcode:'',
			phonecode:'',
			phonetoken:'',
			logining: false,
			realname:'',
			token:'',
			checked:false,
			scode:false,
			getcode:'获取验证码',
			width:70,height:30
		}
	},
	onLoad() {
		var _self = this;
		setTimeout(function(){
			_self.init();
		},100)
	},
	methods: {
		...mapMutations(['bindCn']),
		inputChange(e){
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		quxiao(){
			uni.redirectTo({
				url:"login"
			})
		},
		toNext(){
			this.logining = true;
			if(!this.mobile){
				uni.showToast({ title: '请填写手机号码', icon: 'none' });
				this.logining = false;return false;
			}
			if(!this.imgcode){
				uni.showToast({ title: '请填写图形验证码', icon: 'none' });
				this.logining = false;return false;
			}
			if(!this.phonecode){
				uni.showToast({ title: '请填写手机验证码', icon: 'none' });
				this.logining = false;return false;
			}
			uni.showLoading({
				title: '正在验证',
				mask: true
			});
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/login/forgetyz',
				data: {phone:this.mobile,imgcode:this.imgcode,code:this.phonecode,token:this.phonetoken},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					uni.hideLoading();
					var one = resa.data;
					if(one.code==0){
						uni.showToast({
							title:one.msg,icon:'success',mask:true,duration: 3000
						});
						uni.setStorageSync("cbe_forgetToken",one.token);
						var timer = setTimeout(()=>{
							uni.redirectTo({
								url:"changepwd"
							})
						},3000);
					}else{
						uni.showToast({ title:one.msg, icon: "none" });
						this.logining = false;
					}
				}
			})
		},
		getPhoneCode:function(e){
			if(this.scode==false){
				var reg = /^1[0-9]{10,10}$/;
				if(!reg.test(this.mobile)){
					uni.showToast({ title: '手机号码格式错误', icon: "none" });
				}else{
					if(!this.imgcode){
						uni.showToast({ title: '图形验证码不能为空', icon: 'none' });
						return false;
					}else{
						if(this.imgcode!=this.imgyzcode){
							uni.showToast({ title: '图形验证码错误', icon: 'none' });
							this.logining = false;return false;
						}
					}
					var ntime = 120;
					this.scode = true;
					this.timer = setInterval(() => {
						ntime = ntime-1;
						if(ntime=='0'){
							this.getcode = '获取验证码';
							ntime = 120;
							this.scode = false;
							clearInterval(this.timer);
						}else{
							this.getcode = '重新获取('+ntime+'s)';
						}
					},1000);
					uni.showLoading({
						title: '正在获取',
						mask: true
					});
					uni.request({
						url: uni.getStorageSync('hostname') + '?action=index/common/getphonecode', //获取手机验证码信息,
						data: {phone:this.mobile,use:'forget'},
						method:'POST',
						dataType:'json',
						header: {'content-type' : 'application/x-www-form-urlencoded'},
						success: (resa) => {
							uni.hideLoading();
							var one = resa.data;
							if(one.code == 0){
								uni.showToast({
									title:one.msg,icon:'success',mask:true,duration: 3000
								});
								this.phonetoken = one.token;
							}else{
								uni.showToast({ title:one.msg,icon: "none"});
								this.getcode = '获取验证码';
								ntime = 120;
								this.scode = false;
								clearInterval(this.timer);
							}
							
						},
						fail:(resa)=> {
							uni.showToast({ title: '网络连接超时，请检查您的网络', icon: "none" });
							var timer = setTimeout(()=>{
								this.getcode = '获取验证码';
								ntime = 120;
								this.scode = false;
								clearInterval(this.timer);
							},1000);
						}
					})
				}
			}
		},
		init:function(){
			var context = uni.createCanvasContext('imgcanvas',this),w=this.width,h=this.height;
			context.setFillStyle("white");
			context.setLineWidth(5);			
			context.fillRect(0, 0, w, h);
			var pool=["A","B","C","D","E","F","G","H","J","K","L","M","N","P","Q","R","S","T","U","V","W","S","Y","Z","1","2","3","4","5","6","7","8","9","0"],str='';
			for(var i=0;i<4;i++){
				var c = pool[this.rn(0,pool.length)];//随机的字
				var deg = this.rn(-30,30);//字体的旋转角度
				context.setFontSize(18);
				context.setTextBaseline("top");
				context.setFillStyle(this.rc(80,150));
				context.save();
				context.translate(15*i+15,parseInt(h/1.5));
				context.rotate(deg*Math.PI/180);
				context.fillText(c,-15+5,-15);
				context.restore();
				str+=c;
			}
			uni.setStorage({
				key:'imgcode',
				data:str,
			});
			this.imgyzcode = str;
			for(var i=0;i<40;i++){
				context.beginPath();
				context.arc(this.rn(0,w),this.rn(0,h),1,0,2*Math.PI);
				context.closePath();
				context.setFillStyle(this.rc(150,200));
				context.fill();
			}
			context.draw();			
		},
		rc:function(min,max){
		    var r=this.rn(min,max);
			var g=this.rn(min,max);
			var b=this.rn(min,max);
			return "rgb("+r+","+g+","+b+")";
		},
		rn:function(max,min){
			return parseInt(Math.random()*(max-min))+min;
		},
		refresh:function(){
			this.init();
		},
		canvasIdErrorCallback: function (e) {
		    console.error(e.detail.errMsg)
		}
	}
}
